<template>
    <v-card>
        <v-toolbar class="wbw_back">
            <v-toolbar-title>模型预测</v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-divider></v-divider>
        <v-card class="flex_display center_display" height="100">
            <div style="width:40%;height:80%">
                <v-autocomplete v-model="company" :items="companys" :search-input.sync="changeInfo" outlined
                    label="企业名"></v-autocomplete>
            </div>
            <div style="width:40%;height:80%">
                <v-select v-model="time" :items="times" label="时间" :disabled="select" :loading="loading"
                    outlined></v-select>
            </div>
        </v-card>
        <v-divider></v-divider>
        <div style="width:100%" class="flex_display center_display">
            <v-card width="48%" height="400">
                <v-simple-table fixed-header height="400">
                    <template v-slot:default>
                        <thead>
                            <tr>
                                <th class="text-center">
                                    属性名
                                </th>
                                <th class="text-center">
                                    值
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in datas" :key="item.name">
                                <td>{{ item.name }}</td>
                                <td>{{ item.value }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
                <!-- <v-data-table :headers="headers" :items="datas" hide-default-footer no-data-text="请在上方选择数据" class="elevation-1"></v-data-table> -->
            </v-card>
            <v-spacer></v-spacer>
            <v-card width="48%" height="400">
                <div style="width:100%;margin-top: 20px;gap: 20px;" class="flex_display_col center_display">
                    <h2>模型预测结果</h2>
                    <v-tooltip bottom :color="color">
                        <template v-slot:activator="{ on, attrs }">
                            <v-sheet width="150" height="150" v-bind="attrs" v-on="on">
                                <v-progress-circular :rotate="360" :size="120" :width="15" :value="value" :color="color">{{
                                    grade ?
                                    grade
                                    : '请选择数据' }}</v-progress-circular>
                            </v-sheet>
                        </template>
                        <span>{{ ratting == 0 ? '暂无评级' : ratting }}</span>
                    </v-tooltip>
                </div>
            </v-card>

        </div>
    </v-card>
</template>

<script>



export default {
    data() {
        return {
            dic: {
                1: 'C', 2: 'CC',
                3: 'CCC', 4: 'B-',
                5: 'B', 6: 'B+',
                7: 'BB-', 8: 'BB',
                9: 'BB+', 10: 'BBB-',
                11: 'BBB', 12: 'BBB+',
                13: 'A-', 14: 'A',
                15: 'A+', 16: 'AA-',
                17: 'AA', 18: 'AA+',
                19: 'AAA'
            },
            company: '',
            companys: [],
            changeInfo: null,
            timeId: null,
            timeId1: null,
            select: true,
            loading: false,
            times: [],
            time: '',
            datas: [],
            index: 0,
            grade: '',
            value: 0,
            color: 'grey',
            ratting: 0,
        }
    },
    mounted() {
        this.load();
    },
    methods: {
        async load() {
            const company_url = this.port + "load";
            const re = await this.axios.get(company_url);
            if (re.status != '200') {
                return;
            }
            this.companys = this.parse(re.data, 'company')
        },
        parse(data, prop) {
            let a = [];
            for (let d of data) {
                a.push(JSON.parse(d)[prop]);
            }
            return a;
        },
        parseall(data) {
            let a = [];
            for (let d of data) {
                a.push(JSON.parse(d));
            }
            return a;
        },
        async load_time() {
            const company_url = this.port + "load";
            const re = await this.axios.get(company_url + "/" + this.company)
            if (re.status != '200') {
                this.loading = false;
                return;
            }
            this.times = this.parse(re.data, 'time');
            this.loading = false;
        },
        async get_data() {
            const company_url = this.port + "load";
            const re = await this.axios.get(company_url + "/" + this.company + "/" + this.time);
            if (re.status != '200') {
                this.loading = false;
                return;
            }
            this.index = re.data[0].index
            this.datas = this.parseall(re.data[0].data)
            setTimeout(() => {
                this.get_result();
            }, 2000)
        },
        async get_result() {
            const re = await this.axios.get(this.port + "result/" + this.index);
            if (re.status != '200') {
                return;
            }
            const a = Number(this.parseall(re.data)[0].predict);
            this.ratting = a;
            this.grade = this.dic[a];
            this.value = (a - 1) / 18 * 100;
            this.color = this.get_color(this.value / 100)
        },
        get_color(rate) {
            //红：rgb(255,0,0) 绿：rgb(0,255,0)
            const r = Math.floor(255 * (1 - rate))
            const g = Math.floor(255 * rate)
            return 'rgb(' + r + ',' + g + ',0)'
        }

    },
    watch: {
        changeInfo(val) {
            if (!val) {
                return;
            }
            this.select = true;
            this.value = 0;
            this.grade = "";
            this.color = 'grey';
            this.ratting = 0;
            if (this.timeId) {
                clearTimeout(this.timeId);
            }
            this.timeId = setTimeout(() => {
                this.timeId = null;
                if (!this.companys.includes(val)) {
                    this.select = true;
                    return
                }
                this.select = false;
                this.loading = true;
                this.load_time()
            }, 1000)
        },
        time(val) {
            if (!val) {
                return
            }
            this.value = 0;
            this.ratting = 0;
            this.grade = "";
            this.color = 'grey';
            if (this.timeId1) {
                clearTimeout(this.timeId1)
            }
            this.timeId1 = setTimeout(() => {
                this.get_data()
            }, 1500)
        }
    }
}
</script>


