<template>
    <v-card>
        <v-toolbar class="wbw_back">
            <v-toolbar-title>模型展示</v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-divider></v-divider>
        <v-card>
            <div class="flex_display">
                <v-card width="300" height="500" class="flex_display_col center_display">
                    <div style="width: 250px;height: 100px;">
                        <v-text-field label="训练次数" v-model="epochs"></v-text-field>
                    </div>
                    <div>
                        <v-btn color="success" class="mt-12" @click="train()">训练</v-btn>
                    </div>
                    <v-overlay :absolute=true :value="overlay">
                        <v-progress-circular :rotate="360" :size="100" :width="15" :value="value" :color="color">
                            {{ value }}
                        </v-progress-circular>
                    </v-overlay>
                </v-card>
                <v-divider vertical></v-divider>
                <v-sheet width="800" height="500" class="flex_display center_display" v-if="showChart">
                    <chart :width=600 :height=400 :graph="graph"></chart>
                </v-sheet>
            </div>
        </v-card>
    </v-card>
</template>

<script>
import chart from './chart';


export default {
    data: () => ({
        showChart: false,
        overlay: false,
        interval: {},
        value: 0,
        color: 'red',
        epochs: '',
        datas: [],
        graph: {
            title: '准确率',
            legend: 'accuracy',
            xname: '迭代次数',
            yname: '准确率',
            xAxis: [],
            data: []
        },
        group:0,
    }),
    components: {
        chart
    },
    methods: {
        isNumber(str) {
            if (!str) {
                return false
            }
            const num = Number(str)
            return !(isNaN(num) || num == 0)
        },
        async train() {
            if (!this.isNumber(this.epochs)) {
                console.log('a')
                return;
            }
            this.overlay = true;
            const url = this.port + "model/" + this.epochs;
            const re = await this.axios.get(url);
            if (re.status != '200') {
                this.overlay = false;
                return;
            }
            this.group = re.data
            this.datas = [];
            this.showChart = true;
            const epochs = Number(this.epochs);
            this.getAccuracy(epochs);
        },
        async getAccuracy(epochs) {
            if (this.datas.length == epochs) {
                setTimeout(() => {
                    this.overlay = false;
                    this.value = 0;
                }, 1000)
                return
            }
            const acc_url = this.port+"accuracy";
            const re = await this.axios.get(acc_url+"/"+this.group);
            this.parse(re.data);
            this.calcu_graph();
            this.calcu_rate(epochs);
            setTimeout(() => {
                this.getAccuracy(epochs);
            }, 500);
        },
        parse(data) {
            let a = [];
            for (let d of data) {
                a.push(JSON.parse(d));
            }
            this.datas = a
        },
        calcu_rate(epochs) {
            this.value = Math.floor(this.datas.length / epochs * 100);
            const rate = this.value/100;
            this.color = this.get_color(rate);
        },
        get_color(rate){
            //红：rgb(255,0,0) 绿：rgb(0,255,0)
            const r = Math.floor(255*(1-rate))
            const g = Math.floor(255*rate)
            return 'rgb('+r+','+g+',0)'
        },
        calcu_graph() {
            const datas = this.datas;
            let graph = {
                title: '测试次数与准确率图',
                legend: 'accuracy', 
                xname: '迭代次数/次',
                yname: '准确率/%',
            }
            let xAxis = [];
            let data = [];
            for (let i = 0; i < datas.length; i++) {
                xAxis.push(i + 1);
                data.push(datas[i].accuracy)
            }
            graph.xAxis = xAxis;
            graph.data = data;
            this.graph = graph;
        },
    }
}
</script>

<style scoped>
.v-progress-circular {
    margin: 1rem;
}
</style>


